<template>
  <div class="red">
      <header>
          <div @click="goback()" class="back"></div>
          <div class="red">
              <div class="red-wrap">
                  <div class="red-item">
                      <img src="static/elm/img/elm/red/red.png" alt="">
                  </div>
                  <div class="red-item">
                      <img src="static/elm/img/elm/red/red.png" alt="">
                  </div>
                  <div class="red-item">
                      <img src="static/elm/img/elm/red/red.png" alt="">
                  </div>
              </div>
              <p>暂无外卖节红包可用</p>
          </div>
      </header>
      <div class="list">
        <div class="title">
          <div class="title-item">
              <div>人气好店</div>
              <div>热卖美食都在这</div>
          </div>
          <div class="title-item">
              <div>水果零食</div>
              <div>万千好物低折扣</div>      
          </div>
        </div>
        <shop-list :list='indexlist'></shop-list>
      </div>
      <router-link to="#" class="redicon">
          <img src="static/elm/img/elm/red/redpic.gif" alt="">
      </router-link>
  </div>
</template>
<script>
import api from '@/axios/api.js'
import '@/mock.js'
const shopList = () => import('@/components/shop-list')
export default {
  components: {
    shopList
  },
  data: function () {
    return {
      isShow: false,
      indexlist:[]
    }
  },
  created(){
    api.getlist('/indexlist').then(res => {
        this.indexlist = res.list;
        console.log(res.list)
    })
  },
  mounted() {
    window.onscroll = scroll;
    let a=this;
    function scroll(){
      if (window.scrollY >= 62){
          a.isShow = true;
      }
      else if(window.scrollY < 62){
          a.isShow = false;
      }
    }
  },
  methods: {
    goback: function () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}
</script>

<style scoped>
.red{
    height: 100vh;
    width: 100vw;
}
header{
    position: relative;
    width: 100vw;
    height: 120vw;
    background-repeat:no-repeat;
    background-size:100vw;
    background-image: url(../assets/elm/img/elm/red/elm.png)
}
.back{
    position: absolute;
    top:0;
    left: 5vw;
    width: 15vw;
    height: 15vw;
    background-color:transparent;
    z-index: 2;
}
.red-wrap{
    position: absolute;
    top: 68vw;
    left: 50%;
    transform: translateX(-48%);
    width: 64vw;
}
.red-item{
    width: 20vw;
    float: left;
    margin-right: 2vw;
}
.red-item:last-child{
    margin-right: 0;
}
.red-item img{
    width: 100%;
}
.red p{
    position: absolute;
    top: 97vw;
    left: 50%;
    transform: translateX(-48%);
    width: 64vw;
    text-align: center;
    font-size: 3.5vw;
    color: #356331;
}
.list{
    position: relative;
    top: -8vw;
}
.title{
    width: 100vw;
    height: 18vw;
}
.title-item{
    width: 50vw;
    height: 18vw;
    float: left;
    text-align: center;
    background-repeat:no-repeat;
    background-size:50vw;
    color: white;
    font-size: 3.2vw;
}
.title-item div:first-child{
    margin-top: 6vw;
    font-size: 4.8vw;
    font-weight: 600;
}
.title-item:first-child{
    background-image: url('../assets/elm/img/elm/red/l.png')
}
.title-item:last-child{
    background-image: url('../assets/elm/img/elm/red/r.png')
}
.redicon{
    position: fixed;
    top: 80vh;
    right: 5vw;
    width: 20vw;
}
.redicon img{
    width: 100%;
}
</style>



